// axios.all -> 多个请求，一起返回
axios
  .all([
    axios.get('http://httpbin.org/get', { params: { name: 'yang', ge: 18 } }),
    axios.post('http://httpbin.org/post', { name: 'yang', age: 18 })
  ])
  .then((res) => {
    console.log(res[0])
    console.log(res[1])
  })

// 拦截器
// fn1: 请求发送成功会执行的函数
// fn2: 请求发送失败会执行的函数
axios.interceptors.request.use(
  (config) => {
    // 想做的一些操作符
    // 1. 给请求添加token
    // 2. isLoading动画
    console.log('请求成功的拦截')
    return config
  },
  (err) => {
    console.log('请求发生错误')
    return err
  }
)

// fn1: 数据响应成功会执行的函数(服务器正常的返回了数据 20x)
// fn2: 数据响应失败会执行的函数
axios.interceptors.response.use(
  (res) => {
    console.log('数据响应成功的拦截')
    return res.data
  },
  (err) => {
    console.log('服务器响应失败')
    return err
  }
)

