import type { App } from 'vue'
import {
  ElButton,
  ElInput,
  ElForm,
  ElFormItem,
  ElRadio,
  ElAlert,
  ElAside
} from 'element-plus'

const components = [
  ElButton,
  ElInput,
  ElForm,
  ElFormItem,
  ElRadio,
  ElAlert,
  ElAside
]
// 注册全局ELement-plus组件
export default function (app: App): void {
  components.forEach((component) => {
    app.component(component.name, component)
  })
}
